import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { CardService } from './core/services/card.service';
import { Card } from './core/models/card.model';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {
  title: string;
  imgSrc: SafeResourceUrl;
  cards$: Observable<Card[]>;

  constructor(protected sanitizer: DomSanitizer, private cardSvc: CardService) { }

  ngOnInit() {
    this.title = 'Angular App';
    this.imgSrc = this.sanitizer.bypassSecurityTrustResourceUrl('data:image/svg+xml;base64,');
    this.cards$ = this.cardSvc.getCards();
  }
}
